<template>
  <el-input
    class="w-textarea"
    :style="{ width }"
    :value="value"
    type="textarea"
    v-on="inputListeners"
    v-bind="[$attrs]"
  >
  </el-input>
</template>

<script>
/**
 * WTextarea
 * @desc    ()
 */
export default {
  name: 'WTextarea',
  props: {
    width: {
      type: String,
      default: '180px'
    },
    value: {
      type: String,
      default: ''
    },
    toUpperCase: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    // 
    inputListeners() {
      return Object.assign(
        {},
        // 
        this.$listeners,
        // 
        // 
        {
          //  `v-model` 
          input: value => {
            this.$emit('input', this.toUpperCase ? value.toUpperCase() : value)
          },
          blur: e => {
            let value = e.target.value
              .trim()
              .replace(/\s/g, match =>
                match.charCodeAt(0) === 12288 ? String.fromCharCode(32) : match
              )

            // 
            this.$emit('input', value)
          }
        }
      )
    }
  },
  mounted() {
    console.warn(this.$route.name)
  }
}
</script>

<style lang="less" scoped></style>
