<template>
  <el-autocomplete
    class="WAutoComplete"
    :value="value"
    v-bind="$attrs"
    clearable
    v-on="inputListeners"
  >
    <slot slot="append" name="append" />
    <slot slot="prefix" name="prefix" />
    <slot slot="suffix" name="suffix" />
    <slot slot="prepend" name="prepend" />
  </el-autocomplete>
</template>

<script>
/**
 * WAutoComplete
 * @desc    ()
 */
export default {
  name: 'ZAutoComplete',

  props: {
    value: {
      type: String,
      default: ''
    },
    toUpperCase: {
      type: Boolean,
      default: true
    }
  },

  computed: {
    // 
    inputListeners() {
      const vm = this
      // `Object.assign` 
      return Object.assign(
        {},
        // 
        this.$listeners,
        // 
        // 
        {
          //  `v-model` 
          input: value => {
            vm.$emit('input', this.toUpperCase ? value.toUpperCase() : value)
          },
          blur: e => {
            const value = e.target.value
              .trim()
              .replace(/\s/g, match =>
                match.charCodeAt(0) === 12288 ? String.fromCharCode(32) : match
              )
            // 
            vm.$emit('input', value)
          }
        }
      )
    }
  }
}
</script>
<style lang="less" scoped>
.WAutoComplete {
  width: 180px;
}
</style>
