<template>
  <div>
    <editor
      :api-key="apiKey"
      :init="editConfig"
      :value="content"
      @input="tinymcechange"
    />
    <!-- <div v-html="content"></div> -->
  </div>
</template>

<script>
/**
 * WTinymce
 * @desc    
 */
import Editor from '@tinymce/tinymce-vue'
import axios from 'axios'

export default {
  name: 'WTinymce',
  components: {
    editor: Editor
  },
  props: {
    content: {
      type: String,
      default: ''
    },
    token: {
      type: String,
      default: ''
    },
    base_url: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      apiKey: 'hbwpq4uvkrruhpwgtwzyx7dfvrdq7dbstwh01wrtfuznfoi8', //key
      editConfig: {
        height: 500, //
        language_url: '', //
        language: 'zh_CN', //
        browser_spellcheck: true, // 
        branding: false, // 
        elementpath: true, //
        statusbar: true, // 
        paste_data_images: true, // 
        menubar: true, // menu
        cleanup: false,
        fontsize_formats:
          '12px 14px 16px 18px 20px 24px 26px 28px 30px 32px 36px', //
        font_formats:
          '=Microsoft YaHei,Helvetica Neue;PingFang SC;sans-serif;=PingFang SC,Microsoft YaHei,sans-serif;=simsun;serifsans-serif;Terminal=terminal;monaco;Times New Roman=times new roman;times', //
        file_picker_types: 'image',
        images_upload_credentials: true,
        plugins: [
          'advlist autolink lists link image charmap print preview anchor',
          'searchreplace visualblocks code fullscreen',
          'insertdatetime media table paste code help wordcount codesample'
        ],
        toolbar:
          'fontselect fontsizeselect link lineheight forecolor backcolor bold italic underline strikethrough | alignleft aligncenter alignright alignjustify | image quicklink h2 h3 blockquote table numlist bullist preview fullscreen',
        // images_upload_url:'/services/rpastore/upload/uploadAd',
        // 
        images_upload_handler: (blobInfo, success, failure) => {
          let formdata = new FormData()
          formdata.append('file', blobInfo.blob())
          // 
          // http://hh.xxxx.cn/admin/upload
          axios
            .post(
              `${this.base_url}/services/rpastore/upload/uploadTextImg`,
              formdata,
              {
                headers: {
                  token: this.token
                }
              }
            )
            .then(function (res) {
              const data = res.data.data
              if (data) {
                const str = data.substring(0, 4)
                if (str == 'http') {
                  success(res.data.data)
                } else {
                  success('https://' + res.data.data)
                }
              }
            })
            .catch(res => {
              failure('error')
            })
        }
      }
    }
  },

  mounted() {},
  methods: {
    tinymcechange(e) {
      this.$emit('tinymcechange', e)
    }
  }
}
</script>

<style lang="less" scoped></style>
