<template>
  <el-input
    class="w-input"
    :style="{ width }"
    :value="value"
    v-on="inputListeners"
    v-bind="[$attrs]"
  >
    <slot name="append" slot="append" />
    <slot name="prefix" slot="prefix" />
    <slot name="suffix" slot="suffix" />
    <slot name="prepend" slot="prepend" />
  </el-input>
</template>

<script lang="js">
/**
 * WInput
 * @desc    ()
 */
export default {
  name: 'WInput',
  props: {
    width: {
      type: String,
      default: '180px'
    },
    value: {
      type: String,
      default: ''
    },
    toUpperCase: {
      type: Boolean,
      default: true
    },
    filter: {
      type: Function
    }
  },
  computed: {
    // 
    inputListeners() {
      return Object.assign(
        {},
        // 
        this.$listeners,
        // 
        // 
        {
          //  `v-model` 
          input: (value) => {
            let newVal = value
            if (typeof this.filter === 'function') {
              newVal = this.filter(newVal)
            }
            if (this.toUpperCase) {
              if(newVal === undefined) throw new Error('filter undefined')
              newVal = newVal.toString().toUpperCase()
            }
            this.$emit('input', newVal)
          },
          blur: e => {
            let value = e.target.value
              .trim()
              .replace(/\s/g, match =>
                match.charCodeAt(0) === 12288 ? String.fromCharCode(32) : match
              )

            // 
            this.$emit('input', value)
          }
        }
      )
    }
  },
  mounted() {
    console.warn(this.$route.name)
  }
}
</script>

<style lang="less" scoped>
::v-deep.w-input {
  .el-input__inner {
    .el-input__inner();
  }
}
</style>
